.swiper-button-next,
.swiper-button-prev {
  color: 'inherit';
  background: #f4f4f5e6;
  border-radius: 20px;
  width: 32px;
  height: 32px;
  transition: all 0.2s linear;
  &::after {
    font-size: 16px;
    color: #212433;
  }
  &:hover {
    transform: scale(1.05);
  }
}

.swiper-button-disabled {
  pointer-events: unset !important;
}

.swiper-pagination-bullet {
  background: #d3d3d6;
  &-active {
    background: #212433;
  }
}

#dark {
  .swiper-pagination-bullet {
    background: #4e4f5c;
    &-active {
      background: #f4f4f5;
    }
  }
}

@ant-prefix: store;